/* Style The Dropdown Button */
.dropbtn {
  background-color: #1b7340;
  color: white;
  width: 6em;
  height: 6em;
  font-size: 16px;
  border: none;
  cursor: pointer;
}

.dropbtn div{
  height: 5px;
  background-color: #fff;
  margin: 0.7em;
}

/* The container <div> - needed to position the dropdown content */
.dropdown {
  position: absolute;
  display: inline-block;
  z-index: 120;
  right: 0;
  display: initial;
}

@media only screen and (min-width:1000px) {
    .dropdown {
        display:none;
    }
}

/* Dropdown Content (Hidden by Default) */
.dropdown-content {
  display: none;
  position: absolute;
  right: 6em;
  top: 0;
  background-color: #1b7340;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

/* Links inside the dropdown */
.dropdown-content a {
  color: #fff;
  padding: 1em 1.5em;
  text-decoration: none;
  display: block;
  margin: 0;
}

/* Change color of dropdown links on hover */
.dropdown-content a:hover {background-color: #1ca847}

/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {
  display: block;
}

/* Change the background color of the dropdown button when the dropdown content is shown */
.dropdown:hover .dropbtn {
  background-color: #1ca847;
}


/*for removing bold tables*/
th {
    font-weight:normal;
    padding-right: 1em;
}

.header-icons {
    display:flex;
    justify-content:center;
    text-align:center;
    z-index: 100;
}
.down,.header-icons .icon {
    color:#fff;
    text-align:center
}
.header-icons .icon {
    height:20px;
    padding:10px;
    border:2px solid #fff;
    transition:all .7s;
    width:20px;
    font-size:20px;
    margin:0.2em
}
.header-icons .icon:active,.header-icons .icon:hover {
    color:#1a222c;
    background:#fff
}
@media only screen and (min-width:550px) {
    .header-icons .icon {
        width:35px;
        height:35px;
        font-size:35px
    }
}

.text-icon {
    display:inline-block;
    font-size: 40px;
    line-height: 40px;
    background-color:black;
    color:white;
    width: 40px;
    height: 40px;
    text-align: center;
    vertical-align: bottom;
}

.header-logo{
    position:absolute;
    left:0.5em;
    top:0.5em;
    width: 40vw;
    height: 40vw;
    max-width: 200px;
    max-height: 200px;
    z-index:100
}
.header-links{
    margin:10px;
    color:#fff;text-decoration:none;font-size:15px;margin:10px;
    display: none;
}
.header-menu{
    display: initial;
}

@media only screen and (min-width:550px) {
    .header-logo{
        width: 20vw;
        height: 20vw;
        top:1em;
        left:1em;
    }
    .header-menu{
        display: none;
    }
}
@media only screen and (min-width:1000px) {
    .header-logo{
        width: 15vw;
        height: 15vw;
        top:2em;
        left:2em;
    }
    .header-links{
        display: initial;
    }
}

.link {color:#fff;text-decoration:none;font-size:15px;margin:10px}
@media only screen and (min-width:550px)
{.link{font-size:12pt}}
@media only screen and (min-width:1000px)
{.link{font-size:15pt}}

.header{
    position:absolute;
    z-index:1;
    text-align:center;
    width: 100%;
    top: -10vh;
    color:#fff;
    background-color: #1b7340;
    margin:0 auto;
    padding-bottom: 0.5em;
    padding-top: 14vh;
    font-family: "fff-header";
    transform: skewY(-5deg);
}

.header-lower{
    z-index:1;
    text-align:center;
    height: 20%;
    width: 100%;
    bottom: 4vh;
    color:#fff;
    position:absolute;
    margin:0 auto;
    font-family: "fff-header";
}

.next-strike {
    font-size:16pt;
    display:block;
    line-height:1;
    color:#fff;
    font-family: fff-header;
}
@media only screen and (min-width:550px) {
    .next-strike {
        margin:0;
        font-size:30pt;
    }
}

@media only screen and (min-width:550px) {
    .demands{

        width:48%
    }
    .demands-right {
        position:absolute;
        right:0;
        top: -1.6em;
        width:48%
    }
}

.subgroup{
    float: left;
    width:33%;
    }

.faq{
    font-size: 16pt;
    padding-left: 10vw;
    padding-right: 10vw;
}

.demads-box {
    position:relative;
    margin-bottom: 0em;
}
@media only screen and (min-width:550px) {
    .demands-box {
        position:relative;
        margin-bottom: 10em;
    }
}

.border, .border-top {
    position: static;
    z-index: 10;
    margin-top: 10vh;
    margin-bottom: 10vh;
    left: 0px;
    padding:1em;
    background-color: #1b7340;
    font-size: 15pt;
    color: #fff;
    text-align: center;
    transform: skewY(-5deg);
}
.border-top{
    margin-top: -12em;
}
@media only screen and (min-width:550px) {
    .border-top {
        margin-top:-6em;
    }
}

.border-header {
    position: static;
    z-index: 10;
    left: 0px;
    background-color: #1b7340;
    font-size: 15pt;
    color: #fff;
    text-align: center;
}

.quotation {
    text-align: right;
    display: inline-block;
}

.textbox {

}

.language {
    position: absolute;
    right: 1em;
    top: 1em;
    z-index: 20;
    color:#fff;
    text-decoration:none;
    font-size:20px;
    margin:10px
}

.background {
    position: static;
    z-index: 10;
    left: 0vw;
    width: 100%;
    height: calc(100vh - 96px);
    background-image: url("../img/header.jpg");
    background-size: cover;
    background-position: center;
}

.subpage .header-logo {
    position: relative;
    margin-bottom: 60px;
}
.subpage .page {
    max-width: 80%;
    margin-left: 10%;
}

